<template>
  <b-button type="button" variant="secondary" class="rounded-circle dailer-pad-number" v-if="connection" @click="sendVoiceMessage()" :disabled="!voiceEmail">
    <div class="align-items-center d-flex flex-column justify-content-center text-center">
      <div>
        <i class="fe fe-voicemail"></i>
      </div>
      <p class="mb-0 text">Voicemail</p>
    </div>
  </b-button>
</template>

<script>
  import call from "../../../js/dialer/call";
  import { bus } from "@/js/core/emit-bus";

  export default {
    props: {
      connection: Object,
    },
    data() {
      return {
        voiceEmail: true,
        isVoiceMail: true,
      };
    },
    created() {
      bus.$emit("getVoicemailStatus", (status) => {
        this.isVoiceMail = status;
      });
    },
    methods: {
      async sendVoiceMessage() {
        parent.postMessage({ isVoiceMail: true }, "*");
        if (this.isVoiceMail) {
          await call.sendVoiceEmail(this.connection.parameters.CallSid);
          this.voiceEmail = false;
        }
      },
    },
  };
</script>
